<template>
  <v-card id="SymptomsDialogCard">
    <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
      <v-avatar tile color="white" size="50">
        <v-icon medium :color="plant.vue_color" class="mx-2">visibility</v-icon>
      </v-avatar>
      <span class="ml-2">{{ symptom.name }}</span>
      <v-spacer></v-spacer>
      <slot></slot>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="subheading">{{ symptom.description }}</v-card-text>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['plant', 'symptom'],
  data () {
    return {
    }
  }
}
</script>



