<template>
  <v-card tile class="my-4">
    <v-card-title :class="plant.vue_color || 'secondary'" class="title white--text pa-0">
      <v-avatar tile color="white" size="55" class="ml-0 mr-2">
        <v-icon medium :color="plant.vue_color" class="mx-2">local_hospital</v-icon>
      </v-avatar>
      <span v-if="diseaseNotExist">{{ plant.gr }}</span>
      <template v-else>
        <span class="white--text">{{ plant.gr }}</span>
        <v-icon :color="plant.vue_color + ' lighten-4'">keyboard_arrow_right</v-icon>
        <span class="white--text">{{ disease.name }}</span>
      </template>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <v-list three-line v-if="!diseaseNotExist" class="pa-0">
        <template v-for="(info, i) in D_INFO">
          <DiseaseTile
            :handler="() => false"
            :key="`tile-${i}`"
            :color="plant.vue_color"
            :sub="info.sub"
            :icon="info.icon">
            <span v-html="info.title"></span>
          </DiseaseTile>
        </template>
        <ListGroup icon="security" :color="plant.vue_color" :title="TREATMENT_TITLE" sub="Καταπολέμηση">
          <template slot="items" v-for="(treatment, t) in disease.treatment">
            <v-list-tile :key="`tile-${t}`">
              <v-list-tile-avatar><v-icon :color="plant.vue_color">format_list_bulleted</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ treatment }}.</v-list-tile-title>
                <v-list-tile-sub-title>Νο.{{ t+1 }} μέτρο</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider v-if="t+1 < disease.treatment.length" :key="`d-${t}`"></v-divider>
          </template>
        </ListGroup>
        <v-divider></v-divider>
        <v-subheader :class="plant.vue_color + ' lighten-5'">Κριτήρια στα οποία απαντήσατε</v-subheader>
        <v-divider></v-divider>
        <ListGroup :groupDisabled="steps.length == 0" :title="`Απαντηθέντα κριτήρια (<b>${steps.length}</b>)`" :color="plant.vue_color"
          :sub="`Επιλεγμένες τιμές (<b>${totalValues}</b>)`" icon="library_books">
          <template slot="items">
            <v-list class="pa-0">
              <template v-for="(step, s) in steps">
                <v-list-tile :key="`stepTile_${s}`">
                  <v-list-tile-avatar><v-icon :color="plant.vue_color">format_list_bulleted</v-icon></v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ step.qualifier }}<v-icon class="pb-1">arrow_drop_down</v-icon></v-list-tile-title>
                    <v-list-tile-sub-title>{{ step.selectedValues.join(' / ') }}</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider v-if="s < steps.length-1" inset :key="`stepDivider_${s}`"></v-divider>
              </template>
            </v-list>
          </template>
        </ListGroup>
        <v-divider></v-divider>
        <v-subheader :class="plant.vue_color + ' lighten-5'">Συμπτωματολογία στο φυτό {{ plant.gr }}</v-subheader>
        <v-divider></v-divider>
        <ListGroup :groupDisabled="Object.keys(disease.rules[symptom]).length == 0" v-for="(symptom, s) in Object.keys(disease.rules)" :key="`group-${s}`" :color="plant.vue_color" icon="visibility"
          :title="`Το φυτό παρουσιάζει ${symptom}`" :sub="`Κριτήρια ασθένειας για ${symptom}`" >
          <template slot="items">
            <v-list class="pa-0">
              <template v-for="(rule, r) in Object.keys(disease.rules[symptom])">
                <v-list-tile :key="`tile-${s}-${r}`">
                  <v-list-tile-avatar><v-icon :color="plant.vue_color">notes</v-icon></v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ rule }}<v-icon class="pb-1">arrow_drop_down</v-icon></v-list-tile-title>
                    <v-list-tile-sub-title>{{ disease.rules[symptom][rule].join(' / ') }}</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider v-if="r < Object.keys(disease.rules[symptom]).length-1" :key="`divider-${s}-${r}`"></v-divider>
              </template>
            </v-list>
          </template>
        </ListGroup>
      </v-list>
      <div v-if="diseaseNotExist">
        <v-alert type="error" transition="slide-x-transition" :value="true" class="ma-0">
          Η ασθένεια <em><b>{{ routeDisease }}</b></em>, δεν βρέθηκε στο αρχείο του φυτού {{ plant.gr }}.<br>
          Για πλήρες αρχείο ασθενειών του κηπευτικού, μεταβείτε στη Σελίδα Διάγνωσης πατώντας στο κουμπί <v-icon color="white" class="pb-1">local_florist</v-icon>.
        </v-alert>
        <v-divider :class="plant.vue_color + ' lighten-2'"></v-divider>
        <v-btn class="ma-0" depressed block large color="white" @click="toDiagnosis">
          <v-icon large :color="plant.vue_color">local_florist</v-icon>
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import LoadingPlants from './LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'
import DiseaseTile from '@/components/layouts/DiseaseTile'
import ListGroup from '@/components/layouts/ListGroup'
import _ from 'lodash'

export default {
  name: 'Diseases',
  props: ['plant', 'routeDisease', 'steps'],
  components: {
    LoadingPlants,
    CardLayout,
    DiseaseTile,
    ListGroup
  },
  data () {
    return {
      disease: null,
      selectedPlant: false,
      showTreatment: false
    }
  },
  created () { this.disease = this.plant.diseases.find(d => d.name === this.routeDisease) },
  computed: {
    totalValues () {
      return _.flatMap(this.steps, _.property('selectedValues')).length
    },
    TREATMENT_TITLE () {
      var count = ''
      if (this.disease.treatment.length > 1) {
        count = 'μέτρα'
      } else {
        count = 'μέτρο'
      }
      return `${this.disease.treatment.length} ${count} αντιμετώπισης`
    },
    D_RULES () {
      var rules = []
      for (var symptom in this.disease.rules) {
        for (var rule in this.disease.rules[symptom]) {
          rules.push({
            rule: rule,
            values: this.disease.rules[symptom][rule]
          })
        }
      }
      return rules
    },
    D_INFO () {
      return [{
        icon: 'info',
        title: this.disease.name,
        sub: 'Ονομασία ασθένειας'
      }, {
        icon: 'title',
        title: this.disease.parasitic ? 'Παρασιτική' : 'Μη παρασιτική',
        sub: 'Τύπος ασθένειας'
      }, {
        icon: 'bug_report',
        title: this.disease.parasitic ? `<em>${this.disease.pathogen}</em>` : this.disease.pathogen,
        sub: 'Παθογόνο αίτιο'
      }, {
        icon: 'description',
        title: this.disease.description,
        sub: 'Σύντομη περιγραφή'
      }]
    },
    diseaseNotExist () {
      return this.disease === undefined
    }
  },
  methods: {
    toDiagnosis () {
      this.$router.push('/diagnosis')
    }
  }
}
</script>

<style lang="css">
.nbr{
  border-radius: 0px;
}
.ntd{
  text-decoration: none;
}
</style>



